* {
    font-family: 'Roboto', sans-serif;
    padding: 0em;
    margin: 0em;
    font-size: 23px; 
}

body {
    background-image: linear-gradient(to right, #00CCBF, #72F2EB);
    height: 100%;
}

.header-home {
    display: flex;
    position: relative;
    height: 100%;
    padding: .7em;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: .5em;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);
}

a {
    text-decoration: none;
    color: black;
}


h2{
    text-align: center;
    margin: .8em;
}
.title-header:hover {
    color: #FF5F5D;
    transform: scale(1.10);
}

.main-home h3 {
    text-align: center;
    padding: .5em;    
}

.main-home {
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: .5em;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);
    max-width: 1200px;
    height: 800px;
    margin: auto;
    margin-top: 2%;
}

.header-home > a:hover {
    color: #FF5F5D;
    transform: scale(1.05);
    cursor: pointer;
}

#meuPerfil {
    display: flex;
    justify-content: center;
    align-items: center;
}

#meuPerfil span {
    padding: 0.4em;
}


#btnLoginHeader {
    display: block;
    padding: .4em;
    background-color: #FF5F5D;
    border: 0;
    color: white;
    border-radius: .4em;
    cursor: pointer;
    text-align: center;
    align-items: center;
}

#btnLoginHeader:hover{
    transform: .1s;
    transform: scale(1.1);
    background-color: black;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.473);
}

.container-contatos {
    display: flex;
    flex-direction: column;
}

.btnAd {
    display: flex;
    justify-content: center;
    margin: .8em;
}

.btnADD {
    padding: .8em;
    color: white;
    background-color: black;
    border: 0;
    border-radius: .5em;
    text-align: center;
}

.btnADD:hover {
    transition: .1s;
    transform: scale(1.05);
    cursor: pointer;
    background-color: #FF5F5D;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.473);
}

.table-contatos {
    margin: 1.5em;
}

#contato-void{
    text-align: center;
    color: gray
}

.btnApagarContato{
    padding: .15em;
    background-color: #FF5F5D;
    color: white;
    border: 0;
    border-radius: .5em;
}

.btnApagarContato:hover {
    transform: .1s;
    background-color: black;
    transform: scale(1.05);
    cursor: pointer;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.473);
}`, "",{"version":3,"sources":["webpack://./frontend/assets/styles/home.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAGhB;IACI,iCAAiC;IACjC,YAAY;IACZ,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,6DAA6D;IAC7D,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,8CAA8C;AAClD;;AAEA;IACI,qBAAqB;IACrB,YAAY;AAChB;;;AAGA;IACI,kBAAkB;IAClB,YAAY;AAChB;AACA;IACI,cAAc;IACd,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,8CAA8C;IAC9C,iBAAiB;IACjB,aAAa;IACb,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,sBAAsB;IACtB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,cAAc;AAClB;;;AAGA;IACI,cAAc;IACd,aAAa;IACb,yBAAyB;IACzB,SAAS;IACT,YAAY;IACZ,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,qBAAqB;IACrB,uBAAuB;IACvB,8CAA8C;AAClD;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,YAAY;IACZ,uBAAuB;IACvB,SAAS;IACT,mBAAmB;IACnB,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,eAAe;IACf,yBAAyB;IACzB,8CAA8C;AAClD;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB;AACJ;;AAEA;IACI,cAAc;IACd,yBAAyB;IACzB,YAAY;IACZ,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,uBAAuB;IACvB,sBAAsB;IACtB,eAAe;IACf,8CAA8C;AAClD","sourcesContent":["@charset \"UTF-8\";\r\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');\r\n\r\n* {\r\n    font-family: 'Roboto', sans-serif;\r\n    padding: 0em;\r\n    margin: 0em;\r\n    font-size: 23px; \r\n}\r\n\r\nbody {\r\n    background-image: linear-gradient(to right, #00CCBF, #72F2EB);\r\n    height: 100%;\r\n}\r\n\r\n.header-home {\r\n    display: flex;\r\n    position: relative;\r\n    height: 100%;\r\n    padding: .7em;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    background-color: white;\r\n    border-radius: .5em;\r\n    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);\r\n}\r\n\r\na {\r\n    text-decoration: none;