___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";

* {
    font-family: 'Roboto', sans-serif;
    padding: 0em;
    margin: 0em;
}

body {
    background-image: linear-gradient(to right, #00CCBF, #72F2EB);
    height: 100%;
}

.header-login {
    display: flex;
    position: relative;
    height: 100%;
    padding: .7em;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-radius: .5em;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);
}

.main-login {
    background-color: white;
    display: flex;
    flex-direction: column;
    border-radius: .5em;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);
    align-items: center;
    justify-content: center;
    width: 650px;
    height: 500px;
    margin: auto;
    margin-top: 10%;
}

a {
    text-decoration: none;
    color: black;
}

.container-login {
    margin: 2em;
    padding: 1.2em;
    background-color: #51e9df;
    width: 310px;
    height: 230px;
    border-radius: .5em;
}

.container-login > h3 {
    text-align: center;
    margin: .3em;
}
p {
    margin: .3em;
}

.main-login label {
    margin-right: .5em;
}

.main-login h2{
    padding: .3em;
    align-items: center;
    text-align: center;
    font-size: 1.5em;
    font-weight: 300;
}

.btn-login {
   width: 310px;
    height: 30px;
    background-color: black;
    color: white;
    border: 0;
    border-radius: .5em;
}

input {
    outline: none;
    border-radius: .5em;
}

input:focus {
    border: #FF5F5D 1px solid;
    outline: #df7574 1px solid;
}


.btn-login:hover {
    transition: .1s;
    transform: scale(1.05);
    cursor: pointer;
    background-color: #FF5F5D;
    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.473);
}

.container-login > span, a{
    font-size: .8em;
}


.container-login  a{
    color: black;
    text-decoration-color: gray;
    text-decoration: underline;
}

.container-login a:hover{
    color: #FF5F5D;
}`, "",{"version":3,"sources":["webpack://./frontend/assets/styles/login.css"],"names":[],"mappings":"AAAA,gBAAgB;;AAGhB;IACI,iCAAiC;IACjC,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,6DAA6D;IAC7D,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,uBAAuB;IACvB,mBAAmB;IACnB,8CAA8C;AAClD;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,8CAA8C;IAC9C,mBAAmB;IACnB,uBAAuB;IACvB,YAAY;IACZ,aAAa;IACb,YAAY;IACZ,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,cAAc;IACd,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,YAAY;AAChB;AACA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;GACG,YAAY;IACX,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,0BAA0B;AAC9B;;;AAGA;IACI,eAAe;IACf,sBAAsB;IACtB,eAAe;IACf,yBAAyB;IACzB,8CAA8C;AAClD;;AAEA;IACI,eAAe;AACnB;;;AAGA;IACI,YAAY;IACZ,2BAA2B;IAC3B,0BAA0B;AAC9B;;AAEA;IACI,cAAc;AAClB","sourcesContent":["@charset \"UTF-8\";\r\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');\r\n\r\n* {\r\n    font-family: 'Roboto', sans-serif;\r\n    padding: 0em;\r\n    margin: 0em;\r\n}\r\n\r\nbody {\r\n    background-image: linear-gradient(to right, #00CCBF, #72F2EB);\r\n    height: 100%;\r\n}\r\n\r\n.header-login {\r\n    display: flex;\r\n    position: relative;\r\n    height: 100%;\r\n    padding: .7em;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    background-color: white;\r\n    border-radius: .5em;\r\n    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);\r\n}\r\n\r\n.main-login {\r\n    background-color: white;\r\n    display: flex;\r\n    flex-direction: column;\r\n    border-radius: .5em;\r\n    box-shadow: .3em .3em 1em rgba(0, 0, 0, 0.432);\r\n    align-items: center;\r\n    justify-content: center;\r\n    width: 650px;\r\n    height: 500px;\r\n    margin: auto;\r\n   